    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: opacity 0.3s ease;
}

/*.modal-overlay--open {*/
/*    opacity: 1;*/
/*}*/

/*.modal-overlay--close {*/
/*    opacity: 0;*/
/*}*/

.modal-window {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    min-width: 300px;
    position: relative;
    transition: width 0.3s ease, transform 0.3s ease;
}

.modal-overlay--open {
    opacity: 1;
    transition: opacity 0.3s ease;
}

.modal-overlay--close {
    opacity: 0;
    transition: opacity 0.3s ease;
}

/*.modal-window {*/
/*    background-color: white;*/
