/*    border-radius: 8px;*/
/*    width: 300px;*/
/*    position: relative;*/
/*    transform: scale(0.8);*/
/*    opacity: 0;*/
/*    transition: opacity 0.3s ease, transform 0.3s ease;*/
/*}*/

.modal-window--open {
    transform: scale(1);
    opacity: 1;
}

.modal-window--close {
    transform: scale(0.8);
    opacity: 0;
}

.modal-close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
}

.modal-content {
    padding: 20px;
